.amotos-car {
  .car-title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 10px;
    margin-top: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    a {
      color: inherit;
    }
    &:hover {
      color: var(--amotos-color-accent);
    }
  }
}