$font_size: (
		"10": 10px,
		"11": 11px,
		"12": 12px,
		"13": 13px,
		"14": 14px,
		"15": 15px,
		"16": 16px,
		"18": 18px,
		"20": 20px,
		"21": 21px,
		"24": 24px,
		"30": 30px,
		"31": 31px,
		"32": 33px,
		"36": 36px,
		"38": 38px,
		"46": 46px,
		"50": 50px,
		"60": 60px,
);
@each $k, $v in $font_size {
	.fs-#{$k} {
		font-size: $v !important;
	}
}